import React, {useContext} from 'react'
import {MyContext} from "../context"
import styled from "styled-components"
import {logOut} from '../services/index'
import {Link, Redirect} from "react-router-dom"


const ProfileStyled = styled.div`

margin-top: 80px;
background-color: black;
color: white;
height: 100vh;
text-align: left;

.profile-container{
    padding: 32px;
    text-align: left;
}


ul{
    list-style: none;

}

li{
    font-size: 24px;
    text-align: left;
}

a {
    color: white;
    text-decoration: none;
}

a:hover{
    color: #FF5C00;
}

.container{
    border: 1px white solid;
    height: 100px;
    width: 300px;
    padding: 8px;
    margin-bottom: 16px; 
    display: flex;
    flex-direction: row;
    justify-content: space-between;

}

.guest-name{
    font-size: 12px;
}

.image-container{
    width: 100px;
    height: 100px;
    object-fit: cover;
}

.show-pic{
    width:100px;
    height: 100px;
    object-fit: cover;
}

.admin-button{
background-color: white;
border-radius: 1px solid white;
padding: 8px;
width:120px;
}

.admin-button>a{
    color: black;
}

.logout{
    background-color: white;
    border-radius: 1px solid white;
    padding: 8px;   
    width:120px; 
}

`

function Profile({history}) {

    const {user, clearContextUser} = useContext(MyContext)

    console.log(user)


    async function logOutProcess(){
        await logOut()
        clearContextUser()
        history.push("/")
    }

    
    return user ? (
        <ProfileStyled> 
            {<div className="profile-container">
            <h1>Welcome Back</h1>
            <h3>{user.email}</h3>
            <h4>Your favorite shows</h4>
            {/* <p>{user.showsLoved.title}</p> */}

            
            { user ? ( 
                user.showsLoved.map(show => (
                <div className="container">
                    <div>
                        <p><Link to={`/shows/${show._id}`}>{show.title}</Link></p>
                        <p className="guest-name">by {show.guest}</p>
                    </div>
                  
                     <div className="image-container">
                         <img src={show.image} className="show-pic"/>
                     </div>
                    

                </div>
                
            ))
             ):(
            <p>no shows yet</p>
             )}
        
        

            

            {user.role === "ADMIN" && (<button className="admin-button"><Link to="/admin">ADMIN PANEL</Link></button> )}  

            <button className="logout" onClick={logOutProcess}>LOGOUT</button> 
            </div>     }
         
        </ProfileStyled>
    ) : (
        <Redirect to="/login"/>
    )
}

export default Profile
